body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  position: relative;
  height: 100%;
  border: 1px solid black;
}

.counter {
  position: absolute;
  left: 0;
  bottom: 0;
  font-size: 50px;
  color: #777777;
}

.blue { background-color: #207EBC; }
.red { background-color: #C23724; }
.yellow { background-color: #F3C500; }
.green { background-color: #1AAF5C; }

html,
#root,
.App,
.columns-container {
  position: relative;
  margin: 0;
}

.columns-container {
  height: 100%;
}

.App {
  height: calc(100% - 5em);
  padding-top: 5em;
}
html {
  height: 100%;
}
#root {
  height: 100%;
}


header.MuiAppBar-colorPrimary-8 {
  background-color: #F3C500;
}
.diamond {
  padding: 15%;
}
.diamond-button-wrapper {
  position: relative;
  top: 0;
  left: 0;

  width: 100%;
  height: 0;
  padding: 50% 0;

  -webkit-transform: rotate(45deg);

          transform: rotate(45deg);

  box-shadow: 0px 0px 30px -10px rgba(0,0,0,0.75);
}
.diamond-button-1,
.diamond-button-2,
.diamond-button-3,
.diamond-button-4 {
  position: absolute;
  width: 50%;
  height: 0;
  padding: 25% 0;
  margin: 0;

  outline: none;
  border: 0;
}
.diamond-button-1:hover,
.diamond-button-2:hover,
.diamond-button-3:hover,
.diamond-button-4:hover {
  cursor: pointer;
}
.diamond-button-1:active,
.diamond-button-2:active,
.diamond-button-3:active,
.diamond-button-4:active {
  box-shadow: inset 0px 0px 30px -10px rgba(0,0,0,0.75);
}

.diamond-button-1 {
  top: 0;
  left: 0;
}
.diamond-button-2 {
  top: 0;
  right: 0;
}
.diamond-button-3 {
  bottom: 0;
  right: 0;
}
.diamond-button-4 {
  bottom: 0;
  left: 0;
}

.column {
  width: 32%;
  height: 100%;
  display: block;
  float: left;
  position: relative;
}

.column-diamond-container {
  width: 50%;
  margin: 0 25%;
  position: absolute;
  bottom: 10px;
  left: 0;
}



.column-entry-enter {
  opacity: 0.01;
}

.column-entry-enter.column-entry-enter-active {
  opacity: 1;
  -webkit-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
}

.column-entry-leave {
  opacity: 1;
}

.column-entry-leave.column-entry-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
}


.entry {
  display: block;
  height: 50px;
  border: 1px solid;
  margin-top: 20px;
  margin-left: 10%;
  margin-right: 10%;

  box-shadow: 0px 0px 30px -10px rgba(0,0,0,0.75);
}

